@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 62.5%
}

body {
  margin: 0;
  background: #D3CCE3;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #E9E4F0, #D3CCE3); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}


.main-data{margin:9rem auto}.main-data .container{max-width:70%;margin:auto;display:grid;grid-template-columns:31% 31% 31%;grid-column-gap:3.5%;grid-row-gap:40px;padding:0 30px;align-items:center}.main-data .container .background-green{background-image:linear-gradient(to right top, #068b29, #0a922a, #0f992a, #14a02b, #19a72b, #1cad31, #1fb337, #21b93d, #22bf49, #24c655, #26cc60, #28d36b)}.main-data .container .background-orange{background-image:linear-gradient(to right top, #cf8e13, #d09018, #d1921d, #d29421, #d39625, #d59828, #d79b2c, #d99d2f, #dca033, #dfa436, #e2a73a, #e5ab3d)}.main-data .container .background-red{background-image:linear-gradient(to right top, #9b0909, #a11312, #a71b19, #ad2221, #b32928, #b9302e, #bf3635, #c53c3b, #cc4443, #d34b4a, #da5352, #e15a5a)}.main-data .container .card-body{box-shadow:0px 0px 5px 3px rgba(0,0,0,0.1);text-align:center;border-radius:5%}.main-data .container .card-body-dist{padding:1.5rem;color:black;font-size:2rem;font-family:'Roboto', sans-serif}.main-data .container .card-body-detail{padding:2px}.main-data .container .card-body-detail .sub-detail{margin-bottom:25px}.main-data .container .card-body-detail .sub-detail .title{display:block;margin-bottom:.08rem;margin-right:.08rem;color:black;font-weight:bold;font-size:1.4rem}.main-data .container .card-body-detail .sub-detail:first-child{padding-top:.8rem}.main-data .container .card-body-detail .sub-detail .val{font-size:1.2rem;color:#fff}@media (max-width: 992px){.main-data{margin:2rem auto}.main-data .container{max-width:1200px;margin:auto;display:grid;grid-template-columns:48% 48%;grid-column-gap:4%;grid-row-gap:40px;padding:0 30px}}@media (max-width: 600px){.main-data .container{max-width:1200px;margin:auto;display:unset;padding:0 30px}.main-data .container .card-body{width:80%;margin:18px auto}}

.reset-btn{position:fixed;bottom:1%;width:13%;transform:translate(-50%, -50%);left:50%;padding:10px;background:transparent}.reset-btn .extra{width:100%;font-size:2rem;background:#3F83F1;color:#fff;border:1px #dadada solid;padding:5px 10px;border-radius:2px;font-weight:bold;outline:none}.reset-btn :hover{border:1px #C6C6C6 solid;box-shadow:1px 1px 1px #EAEAEA;color:#333333;background:#F7F7F7}.reset-btn :active{box-shadow:inset 1px 1px 1px #DFDFDF}.spinnnahhh{position:absolute;font-size:20rem;left:45%;top:30%;transform:50%,50%}@media (max-width: 600px){.reset-btn{position:fixed;bottom:0%;width:70%}}

.searchbar{box-shadow:0px 5px 5px -2px rgba(0,0,0,0.1);position:fixed;width:100%;background:white;top:0;position:relative}.searchbar .coronaImg{position:absolute;left:.4rem;top:.7rem}.searchbar #logo{height:8rem;width:13rem}.searchbar .container{max-width:1300px;margin:auto;padding:20px}.searchbar .container .search-box{position:relative;width:98%;margin:auto;max-width:600px;border:1px solid #b6b6b6;border-radius:5px;padding:15px 0 15px 10px}.searchbar .container .search-box .main{display:flex;align-items:center}.searchbar .container .search-box .main .search-input{border-right:none;border:none;width:90%;font-size:1.4rem}.searchbar .container .search-box .main .search-icon{width:10%;display:flex;align-items:center;justify-content:center}.searchbar .container .search-box .result-box{position:absolute;width:100%;top:5rem;box-shadow:0px 4px 5px 3px rgba(0,0,0,0.1);font-size:2rem;background:#fff;left:0}.searchbar .container .search-box .result-box .search-result{padding:15px 20px;background:#fff;border-bottom:1px solid #ccc;cursor:pointer;display:flex;align-items:center;justify-content:space-between}.searchbar .container .search-box .result-box .search-result .name{font-size:2rem;color:grey;font-weight:bold}.searchbar .container .search-box .result-box .search-result:hover{background:#ccc}input:focus{outline:none}.highlight-txt{color:#000}@media (max-width: 992px){.searchbar{position:relative}.searchbar .coronaImg{position:absolute;left:.1rem;top:.6rem}.searchbar #logo{padding:.5rem}.searchbar .container{padding:15px 10px}.searchbar .container .search-box{width:60%}}@media (max-width: 600px){.searchbar{position:relative}.searchbar .coronaImg{position:absolute;left:0rem;top:.6rem}.searchbar #logo{width:12rem;padding:.5rem}.searchbar .container{padding:15px 10px}.searchbar .container .search-box{width:50%}.searchbar .container .search-box .main .search-input{width:85%}.searchbar .container .search-box .main .search-icon{width:15%}}

